
.title{
height: fit-content;
width: fit-content;
background-color: #72B3BF;
border-radius: 8%;
margin-bottom: 8px;
}
.container { border:2px solid #ccc; width:95%; height: 100px; overflow-y: scroll;}
.checkboxstyle{
width: 100%;
box-shadow: inset 0px 1px 8px rgba(0, 0, 0, 0.2);
margin-bottom: 5px;
border: 1px solid #0000004d;
}
.saveBtn{
background-color: #72B3BF;
border-radius: 8%;
border: none;

}



.inputshadow {
  background: #fff;
  color: #525865;
  border-radius: 4px;
  border: 1px solid #d1d1d1;
  box-shadow: inset 0px 1px 8px rgba(0, 0, 0, 0.2);
  font-family: inherit;
  font-size: 1em;
  line-height: 1.45;
  outline: none;
  padding: 0.6em 1.45em 0.7em;
  -webkit-transition: .18s ease-out;
  -moz-transition: .18s ease-out;
  -o-transition: .18s ease-out;
  transition: .18s ease-out;
  height: 29px;
  margin-right: 15px;
}

.inputshadowGender{
  background: #fff;
  color: #525865;
  border-radius: 4px;
  border: 1px solid #d1d1d1;
  box-shadow: inset 0px 1px 8px rgba(0, 0, 0, 0.2);
  font-family: inherit;
  font-size: 1em;
  line-height: 1.45;
  outline: none;
  padding: 0em 1.45em -1.3em;
  -webkit-transition: .18s ease-out;
  -moz-transition: .18s ease-out;
  -o-transition: .18s ease-out;
  transition: .18s ease-out;
  height: 29px;
  margin-right: 15px;

}

.input-group{
  height: 29 !important;
}

